.about {
  &__image {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transform: scale(1.07);
    transition: 0.1s;
    filter: grayscale(100%);
    &__container {
      box-shadow: var(--box-shadow);
      overflow: hidden;
      max-width: 700px;
      width: 100%;
      height: 400px;
      margin: 0 5px 0 0;
      border-radius: 10px;
      transform: translatex(15%) scale(0.9);
      transition: var(--long-transition);
    }
  }
}
